header{
    background-color: rgb(49,70,71);
    color:rgb(250,202,26);
    width: 100%;
    height: 180px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    font-size: 30px;
    
}

header img{
    height: 150px;
    margin-top: 0.5em;
    margin-left: 0.5em;
    margin-bottom: 0.5em;
}




nav ul{ 
    list-style: none;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    padding: 4em;

}

ul li{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    padding: 1em;
}
