@import "menu";
@import "footer";

@mixin h1Sections {
    font-size: 50px;
    margin-top: 1em;
    margin-bottom: 2em;
}

@mixin pSections {
    font-size: 20px;
    line-height: 2;
    padding-bottom: 1rem;
}

@mixin h3Sections{
    font-size: 35px;
    margin-top: 1em;
    margin-bottom: 1em;
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Roboto Serif', sans-serif;
    
}

/* Paleta de colores{
    #3c3c3c,
    #515151,
    #666666,
    #8c8c8c,
    #b5b5b5
}
*/

main{
    background-color: rgb(193,174,109);
}

.container-section1{
//    height: 100vh;
    padding: 1em;
    h1{
        @include h1Sections();
    }
    p{
        @include pSections();
    }
}

.container-section2{
    height: 120vh;
    padding: 1em;
    padding-bottom: 1.5em;
    display: flex;
    flex-direction: column;
    justify-content:space-around;
    
    h1{
        @include h1Sections();
    }
    p{
        @include pSections();
    }
    h3{
        @include h3Sections();
    }
}
.container-ch1{
    padding: 1em;
    display: grid;
    grid-row-gap: 1em;
    grid-column-gap: 1em;
    grid-template-columns: 1fr 3fr;
    img{
        background-color: white;
        width: 150px;
        border-radius: 10px;
        border: 2px solid rgb(49,70,71);
    }
}

.container-ch2{
    padding: 1em;
    display: grid;
    grid-row-gap: 1em;
    grid-column-gap: 1em;
    grid-template-columns: 3fr 1fr;
    img{
        background-color: white;
        width: 500px;
        border-radius: 10px;
        border: 2px solid rgb(49,70,71);
        }
}

.container-section3{
    height: 250vh;
}


